@media (max-width: 768px) {
  main .wrapper nav {
    position: fixed;
    left: 0;
    right: 0;
    z-index: 99;
    border-bottom: 1px solid #e2e2e2;
    padding: 10px 0px !important;
    left: 0;
    background-color: var(--color-white);
  }
}
main .wrapper nav .burger {
  position: relative;
  display: none;
  cursor: pointer;
  width: 20px;
  height: 1.063rem;
  border: none;
  outline: none;
  opacity: 0;
  visibility: hidden;
  background: none;
  transform: rotate(0deg);
  transition: 0.35s ease-in-out;
}
@media (max-width: 768px) {
  main .wrapper nav .burger {
    display: block;
    opacity: 1;
    visibility: visible;
  }
}
main .wrapper nav .burger-line {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  border: none;
  outline: none;
  opacity: 1;
  border-radius: 2px;
  background: var(--color-black);
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}
main .wrapper nav .burger-line:nth-child(1) {
  top: 0;
}
main .wrapper nav .burger-line:nth-child(2), main .wrapper nav .burger-line:nth-child(3) {
  top: 0.5rem;
}
main .wrapper nav .burger-line:nth-child(4) {
  top: 1rem;
}
main .wrapper nav .burger.active .burger-line:nth-child(1), main .wrapper nav .burger.active .burger-line:nth-child(4) {
  top: 1.25rem;
  width: 0%;
  left: 50%;
}
main .wrapper nav .burger.active .burger-line:nth-child(2) {
  transform: rotate(45deg);
}
main .wrapper nav .burger.active .burger-line:nth-child(3) {
  transform: rotate(-45deg);
}
